import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import en from "./translation/en.json";
import chinese from "./translation/chinese.json";

const resources = {
  en: {
    translation: en,
  },
  chinese: {
    translation: chinese,
  },
};

i18n.use(initReactI18next).init({
  resources,
  lng: "en",
  keySeparator: false,
  interpolation: {
    escapeValue: false,
  },
});

export default i18n;
